.modal {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    background-color: rgba(0,0,0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
}
.container {
    background-color: #ffffff;
    width: 60%;
    height: 60%;
    color: black;
    border-radius: 20px;
    align-items: center;
}
.close {
    background-color: transparent;
    outline: none;
    width: 32px;
    height: 32px;
    border: none;
    right: calc(-100% + 45px);
    cursor: pointer;
    display: flex;
    align-items: center;
    top: 16px;
    position: relative;
}
.close:before, .close:after{
content: '';
position: absolute;
width: 2.5px;
height: 24px;
background-color: black;
}
.close:before {
    transform: rotate(45deg);
}
.close:after {
    transform: rotate(-45deg);
}